import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';
import { StepComponent } from './pages/report/step/step.component';
import { SleepComponent } from './pages/report/sleep/sleep.component';
import { HeartrateComponent } from './pages/report/heartrate/heartrate.component';
import { BloodpressureComponent } from './pages/report/bloodpressure/bloodpressure.component';
import { SportsComponent } from './pages/report/sports/sports.component';
import { UsersComponent } from './pages/users/users.component';
import { DevicesComponent } from './pages/devices/devices.component';
import { DropzoneModule } from 'ngx-dropzone-wrapper';
import { AdminComponent } from './pages/admin/admin.component';
import { AuthenticationModule } from './authentication/authentication.module';

@NgModule({
    declarations: [
        AppComponent,
        StepComponent,
        SleepComponent,
        HeartrateComponent,
        BloodpressureComponent,
        SportsComponent,
        UsersComponent,
        DevicesComponent,
        AdminComponent
    ],
    imports: [
        BrowserModule,
        AppRoutingModule,
        DropzoneModule,
        AuthenticationModule
    ],
    providers: [],
    bootstrap: [AppComponent]
})
export class AppModule { }



