import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { StepComponent } from './pages/report/step/step.component';
import { SleepComponent } from './pages/report/sleep/sleep.component';
import { HeartrateComponent } from './pages/report/heartrate/heartrate.component';
import { BloodpressureComponent } from './pages/report/bloodpressure/bloodpressure.component';
import { SportsComponent } from './pages/report/sports/sports.component';
import { UsersComponent } from './pages/users/users.component';
import { DevicesComponent } from './pages/devices/devices.component';
import { AdminComponent } from './pages/admin/admin.component';
import { SignInComponent } from './authentication/sign-in/sign-in.component';
import { ForgotPasswordComponent } from './authentication/forgot-password/forgot-password.component';

const routes: Routes = [
    { path: '', redirectTo: 'report/step', pathMatch: 'full' },
    { path: 'report/step', component: StepComponent },
    { path: 'report/sleep', component: SleepComponent, data: { preload: true } },
    { path: 'report/heartrate', component: HeartrateComponent, data: { preload: true } },
    { path: 'report/bloodpressure', component: BloodpressureComponent, data: { preload: true } },
    { path: 'report/sports', component: SportsComponent, data: { preload: true } },
    { path: 'users', component: UsersComponent, data: { preload: true } },
    { path: 'devices', component: DevicesComponent, data: { preload: true } },
    { path: 'admin', component: AdminComponent, data: { preload: true } },
    { path: 'authentication/sign-in', component: SignInComponent },
    { path: 'authentication/forgot-password', component: ForgotPasswordComponent }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }



