import { Component, OnInit } from '@angular/core';

declare const $: any;
declare const Morris: any;

@Component({
    selector: 'app-step',
    templateUrl: './step.component.html',
    styleUrls: ['./step.component.css']
})
export class StepComponent implements OnInit {

    public chart = true;

    constructor() { }

    ngOnInit() {

        $('.js-exportable').DataTable({
            dom: 'frtip',
            bFilter: false,
            oLanguage: {
                oPaginate: {
                    sFirst: '第一頁',
                    sLast: '最後一頁',
                    sNext: '下一頁',
                    sPrevious: '上一頁'
                },
                sEmptyTable: '沒有資料顯示',
                sInfo: '顯示 _START_ to _END_ of _TOTAL_ 筆資料'
            }
        });

        MorrisBarChart();

        // Morris bar chart
        function MorrisBarChart() {
            Morris.Bar({
                element: 'm_bar_chart',
                data: [{
                    y: '2011',
                    a: 80
                }, {
                    y: '2012',
                    a: 75
                }, {
                    y: '2013',
                    a: 59
                }, {
                    y: '2014',
                    a: 75
                }, {
                    y: '2015',
                    a: 55
                }, {
                    y: '2016',
                    a: 75
                }, {
                    y: '2017',
                    a: 87
                }],
                xkey: 'y',
                ykeys: ['a'],
                labels: ['A'],
                barColors: ['#ffcc80'],
                hideHover: 'auto',
                gridLineColor: '#eef0f2',
                resize: true
            });
        }
    }

    public switchDiagram() {
        this.chart = !this.chart;
    }
}



